@import '../variables';

.line-cart {
    display: flex;
    align-items: flex-start;
    font-size: 1.4rem;
    padding: 1.6rem 0;
    border-bottom: 1px solid $dark;
    &:last-of-type {
        border-bottom: none;
    }

    .line-cart-infos {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex: 1;
        margin: 0 1.6rem;

        h3 {
            font-size: 1.8rem;
            margin-bottom: 0.8rem;
        }

        .mini-icon-container {
            width: 2.4rem;
            height: 2.4rem;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
        }

        .counter {
            display: flex;
            align-items: center;
            margin-bottom: 1.6rem;
            border: 1px solid $dark;
            height: 3.2rem;
            border-radius: 1.6rem;
            padding: 0 0.8rem;

            p {
                font-size: 1.6rem;
                font-weight: bold;
                margin: 0 8px;
            }
        }

        .price {
            font-weight: bold;
        }
    }
    .mini-image {
        object-fit: cover;
        object-position: center center;
        width: 30%;
    }

}
